              >
                <div class="d-flex">
                  <a @click="remove(item)" class="del"
                    ><img src="@/main/assets/images/del.svg" alt=""
                  /></a>
                  <figure>
                    <img :src="item.image" alt="" />
                  </figure>
                  <div class="caption">
                    <h2>{{ item.name }}</h2>
                    <div class="d-flex justify-content-between">
                      <div class="price">
                        {{ item.item_price - item.discount }}
                        $ <br />
                        <span
                          v-if="item.discount > 0"
                          style="
                            float: left;
                            color: #9f9f9f;
                            text-decoration: line-through;
                            font-weight: 500;
                          "
                        >
                          {{ item.item_price }}
                          $
                        </span>
                      </div>
                      <div class="quantity d-flex align-items-center">
                        <div id="quantity" class="d-flex align-items-center">
                          <button
                            @click="decrement(item)"
                            class="btn-subtract"
                            type="button"
                          >
                            -
                          </button>
                          <input
                            type="number"
                            class="item-quantity"
                            min="0"
                            :value="item.item_quantity"
                          />
                          <button
                            @click="increment(item)"
                            class="btn-add"
                            type="button"
                          >
                            +
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <form action="" class="copon" @submit.prevent="addCoupon">
              <input
                type="text"
                :placeholder="$t('Enter_the_coupon_code')"
                v-model="item.coupon"
              />
              <button>{{ $t("Activate_the_coupon") }}</button>
            </form>
            <div class="footer">
              <ul>
                <li>
                  {{ $t("number_of_products") }}
                  <span>{{
                    order.items
                      ? order.items.reduce((c, n) => c + n.item_quantity, 0)
                      : 0
                  }}</span>
                </li>
                <li>
                  {{ $t("total_summation") }}
                  <span>
                    {{ totals.total }}
                    $</span
                  >
                </li>
                <li>
                  {{ $t("Discount") }}
                  <span>
                    {{ totals.discount }}
                    $</span
                  >
                </li>
                <li>
                  {{ $t("Delivery_price") }}
                  <span
                    v-if="totals.total_taxed > limit_shipment"
                    style="
                      float: left;
                      color: #9f9f9f;
                      text-decoration: line-through;
                      font-weight: 500;
                    "
                  >
                    {{ total_shipment }}
                    $
                  </span>
                  <span v-else> {{ total_shipment }} $</span>
                </li>
                <li class="toot">
                  {{ $t("total_summation") }}
                  <span v-if="totals.total_taxed > limit_shipment">
                    {{ totals.total_taxed.toFixed(2) }}
                    $</span
                  >
                  <span v-else>
                    {{ (totals.total_taxed + total_shipment).toFixed(2) }}
                    $</span
                  >
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="col-md-8">
          <div class="block">
            <h4>{{ $t("Delivery_details") }}</h4>
            <div class="row justify-content-center">
              <div class="col-md-11">
                <div class="content">
                  <p>
                    {{ $t("register_as") }} : <strong>{{ user.name }}</strong>
                  </p>
                  <div class="box">
                    <div class="entry-content-myaccount address">
                      <h2>{{ $t("my_addresses") }}</h2>
                      <div class="items">
                        <div
                          class="box address"
                          v-for="address in addresses"
                          :key="address.id"
                        >
                          <div class="d-flex">
                            <div class="form-check">
                              <input
                                class="form-check-input"
                                type="checkbox"
                                value=""
                                id="flexCheckDefault"
                                v-model="address.checked"
                                @input="checkAddress(address)"
                              />
                              <label
                                class="form-check-label"
                                for="flexCheckDefault"
                              >
                              </label>
                            </div>
                            <div>
                              <ul>
                                <li>
                                  {{ address.area }} - {{ address.widget }} -
                                  {{ address.street }} - {{ address.avenue }} -
                                  {{ address.house_number }} -
                                  {{ address.floor_no }} -
                                  {{ address.apartment_number }}
                                </li>
                                <li>
                                  {{ address.notes }}
                                </li>
                              </ul>
                            </div>
                          </div>
                        </div>
                      </div>
                      <a
                        href="/my-account/my_addresses"
                        class="button online"
                        >{{ $t("add_site_new") }}</a
                      >
                    </div>
                    <div class="pay-box form">
                      <h6>  </h6>
                      <form @submit.prevent="save">
                        <div class="d-flex">
                          <!-- <div class="form-check form-check-inline">
                            <input
                              class="form-check-input"
                              type="radio"
                              name="inlineRadioOptions"
                              id="inlineRadio1"
                              value="option1"
                            />
                            <label class="form-check-label" for="inlineRadio1"
                              > 
                              <img
                                src="@/main/assets/images/magento2-knet-payment-gateway.jpg"
                                alt=""
                            /></label>
                          </div>
                          <div class="form-check form-check-inline">
                            <input
                              class="form-check-input"
                              type="radio"
                              name="inlineRadioOptions"
                              id="inlineRadio2"
                              value="option2"
                            />
                            <label class="form-check-label" for="inlineRadio2"
                              >  
                              <img src="@/main/assets/images/visa.jpg" alt=""
                            /></label>
                          </div> -->
                          <div
                            :key="index"
                            v-for="(shipment, index) in shipments"
                            class="form-check form-check-inline"
                          >
                            <input
                              class="form-check-input"
                              type="radio"
                              name="inlineRadioOptions"
                              id="inlineRadio3"
                              v-model="selected_shipment"
                              :value="shipment.id"
                            />
                            <label class="form-check-label" for="inlineRadio3">
                              {{ shipment.name }}
                              <img src="@/main/assets/images/wallet.jpg" alt=""
                            /></label>
                          </div>
                        </div>

                        <button :disabled="!validated" class="button">
                          {{ $t("Complete_the_order") }}
                        </button>
                      </form>
                      <p>{{ $t("agree_condision") }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row justify-content-center infoo">
        <div class="col-md-10 text-center">
          <p>{{ $t("pyment_safe") }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  mounted() {
    if (!this.$attrs.id) return this.$router.push("/");
    this.$store.dispatch("address/index");
    this.$store.dispatch("shipping/index");
    // if (this.$attrs.id) {
    //   this.$store.dispatch("order/show", { id: this.$attrs.id });
    // } else {
    //   this.$router.push("/");
    // }
  },
  data() {
    return {
      selected_shipment: null,
      valid_selected_shipment: false,
      total_shipment: 0,
      my_address: null,
      ids: [],
      item: {
        shipment_price: 0,
        discount: 0,
        total: 0,
        item_quantity: 1,
      },
    };
  },
  methods: {
    remove(item) {
      this.$store.dispatch("cart/removeItem", item);
    },

    increment(item) {
      this.$store.dispatch("cart/incrementItem", item);
    },
    decrement(item) {
      this.$store.dispatch("cart/decrementItem", item);
    },
    getTime() {
      var date = new Date();
      return date.toISOString().slice(0, 19).replace("T", " ");
    },
    async save() {
      let order_copy = JSON.parse(JSON.stringify(this.order));
      delete order_copy.items;
      console.log(order_copy);
      order_copy.due_date = this.getTime();
      order_copy.status = 1;
      order_copy.taxed_total = this.totals.total_taxed;
      order_copy.discount = this.totals.discount;
      order_copy.total = this.totals.total;
      if (this.totals.total_taxed < this.limit_shipment) {
        order_copy.taxed_total + this.total_shipment;
      }
      await this.$store
        .dispatch("order/store", order_copy)
        .then((data) => {
          return data;
        })
        .then(() => {
          let domain = `${process.env.URL || window.location.protocol}//${
            window.location.host
          }`;
          window.open(`${domain}/complete-order/${this.order.id}`);
          window.location.reload();
        });

      // if (order_id) {

      // }
    },
    addCoupon() {
      this.$store
        .dispatch("coupon/show", { id: this.item.coupon })
        .then((data) => {
          this.$store.dispatch("cart/setDiscount", data.value);
        });
    },
    checkAddress(address) {
      this.my_address = address.id;
      this.addresses
        .filter((i) => i.id != address.id)
        .map((i) => (i.checked = false));
    },
  },
  computed: {
    ...mapState({
      order: (state) => state.cart.order,
      user: (state) => state.auth.user.user,
      addresses: (state) => state.address.all,
      settings: (state) => state.setting.all || [],
      shipments: (state) => state.shipping.all || [],
      shippingas: (state) => state.shippinga.all || [],
    }),
    validated() {
      if (
        this.my_address &&
        this.selected_shipment &&
        this.valid_selected_shipment
      ) {
        return true;
      }
      return false;
    },
    totals() {
      let allTotlas = {
        total: 0,
        discount: 0,
        total_taxed: 0,
      };
      if (this.order.items) {
        let all_items = this.order.items;

        allTotlas.total = all_items.reduce(
          (c, n) => c + n.item_price * n.item_quantity,
          0
        );
        allTotlas.discount = all_items.reduce(
          (c, n) => c + n.discount * n.item_quantity,
          0
        );
        allTotlas.total_taxed = all_items.reduce(
          (c, n) =>
            c +
            (n.item_price * n.item_quantity - n.discount * n.item_quantity) *
              (n.tax_percentage / 100 + 1),
          0
        );
      }
      return allTotlas;
    },
    limit_shipment() {
      var price = this.settings.find(
        (v) => v.key == "limit_price_for_shipment"
      ).value;
      return parseFloat(price);
    },
    shipment_price() {
      var price = this.settings.find((v) => v.key == "shipment_amount").value;
      return parseFloat(price);
    },
  },
  watch: {
    order(val) {
      if (val) {
        this.ids = val.items.map((v) => v.item_id);
      }
    },
    selected_shipment(val) {
      if (val && this.ids) {
        this.$store.dispatch("shippinga/index", { ids: this.ids });
      }
    },
    shippingas(val) {
      if (val.length > 0) {
        if (val.length != this.order.items.length) {
          this.valid_selected_shipment = false;
          alert("cant choose this shipment method");
        } else {
          this.valid_selected_shipment = true;
          this.total_shipment = val.reduce((c, n) => c + n.price, 0);
        }
      }
    },
  },
};
</script>
